// 增加默认值，兼容一些怪异机型上微信拿不到对应属性的Bug
export const appendParam = (url = '', params: { [key: string]: any; } = {}) => {
    const sp = url.indexOf('?') === -1 ? '?' : '&';
    const arr = [];

    for (const key of Object.keys(params)) {
        const value = params[key] || '';
        arr.push(`${encodeURIComponent(key)}=${encodeURIComponent(value)}`);
    }

    if (arr.length) {
        url += sp + arr.join('&');
    }
    return url;
};

export const getMappingPage = (page: Mina.PageInstance, urlMap?: any) => {
    return {
        page: page.route,
        params: page.options,
    };
};
