import storage from './storage';

const KEY_COOKIE = 'cookie';

class Cookie {
    get cookie() {
        return storage.get(KEY_COOKIE) || {};
    }

    /**
     *
     * 设置 cookie value
     * @param {string} key 设置的key
     * @param {*} value 设置的值
     * @param {number} [expirationTime] 过期时间，超过后这个 key 会清空，value 为 null
     * @param {boolean} [sync] 是否同步设置cookie
     */
    set(key: any, value: any, expirationTime?: number, sync?: boolean) {
        // 性能优化: 无更新的值不 set
        if (value === this.get(key)) {
            return;
        }

        let newCookie = this.cookie;

        if (typeof key === 'object') {
            newCookie = {
                ...newCookie,
                ...key,
            };
        } else {
            newCookie[key] = value;
        }

        storage.set(KEY_COOKIE, newCookie, expirationTime, sync);
    }

    get(key: string) {
        return this.cookie[key];
    }

    remove(key: string) {
        const { value, expirationTime } = storage.getCompleteStorage(KEY_COOKIE);
        const result = value[key] && delete value[key];

        storage.set(KEY_COOKIE, value, expirationTime);
        return result;
    }

    toCookieString() {
        const arr = [];

        for (let key in this.cookie) {
            let value;
            try {
                key = key.replace(/[^#$&+^`|]/g, encodeURIComponent);
                key = key.replace(/\(/g, '%28').replace(/\)/g, '%29');
                value = (this.cookie[key] + '').replace(
                    /[^!#$&-+\--:<-[\]-~]/g,
                    encodeURIComponent
                );
            } catch (e) {
                // cookie 里的 value 可能会导致 encodeURIComponent 报错
                // https://developer.mozilla.org/en-US/docs/Web/JavaScript/Reference/Errors/Malformed_URI
                // TODO 可以单独上报 error 的时候进行上报，暂时用 console.log 是为了防止呑错
                console.log(e);
                continue;
            }

            arr.push(key + '=' + value);
        }

        return arr.join('; ');
    }
}

export default new Cookie();
