/**
 * 将相对时间转换为时间戳
 * 年(y), 月(M)、日(d)、小时(h)、分(m)、秒(s)
 * 如传入 '23h-59m-59s' 会返回调用函数开始后 23 小时 50 分 59 秒后的时间戳
 *
 * @param {string} relativeTime 相对时间
 * @return {number} 转换后的时间戳
 */
import { mp, isAlipay } from './global';

const relativeTimeToTimestamp = function () {
    const validUnit = 'yMdhms'; // 不考虑精度要求非常严格的情况，如需要，另外写方法

    const yearTimeNumber = 31536000; // 1y = 60 * 60 * 24 * 365，精度要求没有那么严格，所以年都按 365 天算

    const monthTimeNumber = 2592000; // 1m = 60 * 60 * 24 * 30，精度要求没有那么严格，所以月都按 30 天算

    const dayTimeNumber = 86400; // 1d = 60 * 60 *24

    const hourTimeNumber = 3600; // 1h = 60 * 60

    return function (relativeTime: string) {
      if (typeof relativeTime !== 'string') {
        throw Error("The relativeTime must be an string but received ".concat(relativeTime));
      }

      var currentTime = Date.now();
      var timeCount = 0;
      relativeTime.split('-').forEach(function (time) {
        var timeNumber = parseInt(time.slice(0, -1), 10);
        var timeUnit = time[time.length - 1]!;

        if (/\D/.test(time.slice(0, -1)) || typeof timeNumber !== 'number' || validUnit.indexOf(timeUnit) === -1) {
          throw Error("The releated time you passed '" + relativeTime + "' is not valid. It should use '-' as the separator, and should be number except the last char which should be y|M|d|h|m|s of each item");
        }

        switch (timeUnit) {
          case 'y':
            timeCount += yearTimeNumber * timeNumber;
            break;

          case 'M':
            timeCount += monthTimeNumber * timeNumber;
            break;

          case 'd':
            timeCount += dayTimeNumber * timeNumber;
            break;

          case 'h':
            timeCount += hourTimeNumber * timeNumber;
            break;

          case 'm':
            timeCount += 60 * timeNumber;
            break;

          case 's':
            timeCount += timeNumber;
            break;
        }
      });
      return currentTime + timeCount * 1000;
    };
}();
// 抹平小程序接口差异
const getStorageSync = (key: string) => {
    if (isAlipay) {
        const res = mp.getStorageSync({ key });
        // 支付宝平台会返回一个 success 值，但是目前测试的结果这个始终是 true。当没有存储数据的时候，其它平台会返回空字符串。
        return res.data !== null ? res.data : '';
    } else {
        const res = mp.getStorageSync(key);
        return res;
    }
};

const setStorageSync = (key: string, data: any) => {
    if (isAlipay) {
        mp.setStorageSync({
            key,
            data,
        });
    } else {
        mp.setStorageSync(key, data);
    }
}

const prefix = 'ks_storage_';
/*
 * @Author: AndySong
 * @Date: 2018-07-30 22:26:21
 * @LastEditors: AndySong
 * @LastEditTime: 2018-08-02 02:48:52
 * @Description: 本地存储包装
 */
class Storage {
    cache: { [key: string]: any } = {}
    /**
     *
     * 获取 storage 完整信息
     * @param {*} key
     */
    getCompleteStorage(key: string) {
        let item = null;
        const fullKey = prefix + key;
        try {
            // cache 中没有就同步到 cache
            if (this.cache[fullKey] == null) {
                this.cache[fullKey] = getStorageSync(fullKey);
            }
            item = this.cache[fullKey];
        } catch (e) {
            console.error(`getCompleteStorage('${fullKey}')`, e);
        }

        return item;
    }

    /**
     *
     * 获取 storage value
     * @param {*} key
     */
    get(key: string) {
        let value = null;

        try {
            const item = this.getCompleteStorage(key);
            if (typeof item === 'object' && item.isSDKStorage) {
                const { value: realValue, expirationTime } = item;

                // 过期删除
                if (expirationTime && hasExpried(expirationTime)) {
                    this.remove(key);
                } else {
                    value = realValue;
                }
            } else {
                value = item;
            }
        } catch (e) {
            console.error(`get('${prefix + key}')`, e);
        }

        return value;
    }

    /**
     *
     * 设置 storage value
     * @param {string} key 设置的key
     * @param {*} value 设置的值
     * @param {number} [expirationTime] 过期时间，超过后这个 key 会清空，value 为 null
     * @param {boolean} [sync] 是否同步设置storage
     */
    set(key: string, value: any, expirationTime?: string | number, sync = false) {
        let status = false;
        const fullKey = prefix + key;
        try {
            expirationTime =
                expirationTime &&
                (isAllNumber(expirationTime)
                    ? Number(expirationTime)
                    : relativeTimeToTimestamp(expirationTime + ''));

            const storage = {
                value,
                isSDKStorage: true,
                expirationTime,
            };
            // 性能优化: 无更新的「基本数据类型值」不需要 set;「引用数据类型值」需要调用方处理优化
            const valueNoChange =
                ['number', 'string', 'boolean'].indexOf(typeof value) > -1 &&
                value === this.get(key);
            if (!valueNoChange) {
                if (sync) {
                    setStorageSync(fullKey, storage);
                } else {
                    mp.setStorage({
                        key: fullKey,
                        data: storage,
                        fail: (e: Error) => {
                            // fail 情况下, cache 和 storage 不同步, 上报
                            console.error(
                                'mp.setStorage failed: ',
                                { key: fullKey, value },
                                e
                            );
                        },
                    });
                }
                this.cache[fullKey] = storage;
            }
            status = true;
        } catch (e) {
            console.error('set(...), arguments: ', { key: fullKey, value }, e);
        }

        return status;
    }

    /**
     *
     * 删除 storage
     * @param {string} key
     */
    remove(key: string) {
        let status = false;
        const fullKey = prefix + key;
        try {
            delete this.cache[fullKey];
            mp.removeStorage({
                key: fullKey,
                fail: (e: Error) => {
                    // fail 情况下, cache 和 storage 不同步, 上报
                    console.error('mp.removeStorage failed', e);
                },
            });
            status = true;
        } catch (e) {
            console.error(`remove('${fullKey}')`, e);
        }

        return status;
    }

    /**
     *
     * 清除 storage
     */
    clear() {
        let status = false;

        try {
            this.cache = {};
            mp.clearStorage({
                fail: (e: Error) => {
                    // fail 情况下, cache 和 storage 不同步, 上报
                    console.error('mp.clearStorage failed', e);
                },
            });
            status = true;
        } catch (e) {
            console.error(`clear('${prefix}')`, e);
        }

        return status;
    }
}

function hasExpried(expirationTime: string | number) {

    if (expirationTime === undefined || /\D/.test(expirationTime + '')) {
        return true;
    }

    return expirationTime < Date.now();
}

function isAllNumber(value: string | number) {
    if (typeof value === 'number') {
        value = '' + value;
    }

    return !/\D/.test(value);
}

export default new Storage();
