import { appendParam } from '@/util/mina/utils';
import { getSystemInfo } from '@/util/mina/systeminfo';
import { initHeartBeatIncrementId } from '@/util/mina/increment-id';

import {
    getStore,
    setStore,
} from '@/util/mina/store';

import * as HeartBeat from './heart-beat';
import { getPositiveInteger } from '@/util';

enum STORE_KEY {
    SHOW_TIME = 'app_show_time',
    HIDE_TIME = 'app_hide_time',
    STAY = 'stay_length',
}

enum STATUS {
    C = 1,
    H = 2,
    E = 2,
}

enum EVENT_NAME {
    L = 'LAUNCH_EVENT',
    U = 'APP_USAGE_STAT_EVENT',
    E = 'EXCEPTION_EVENT',
    S = 'SHOW',
    D = 'DEVICE_STAT_EVENT',
}

function genLaunchData(weblog: any, target: string, scene: any, cold: boolean) {
    return {
        cold,
        target,
        mode: cold ? STATUS.C : STATUS.H,
        source: scene,
        detail: JSON.stringify({ scene }),
        event_name: EVENT_NAME.L,
    };
}

let isSendFirst = false;

export default (report: any, weblog: any, launchIntervalLimit: number = 30000, backgroundWaitingLimit: number = 0) => {
    let appTimer: any = 0;
    let heartBeatTimer: any = 0;
    const result = {
        onLaunch: [() => {
            getSystemInfo((info) => {
                if (!info) return;
                const { system, model, screenWidth, screenHeight, batteryLevel } = info;
                const log = {
                    os_version: system,
                    model,
                    density_dpi: 0, // 暂时传0， 小程序获取不到dpi
                    screen_width: getPositiveInteger(screenWidth),
                    screen_height: getPositiveInteger(screenHeight),
                    battery: batteryLevel,
                    event_name: EVENT_NAME.D,
                };
                report(log);
            });
        }, () => {
            // 设置初始timestamp
            HeartBeat.setPrevTimestamp();
            // 监听updateSessionId，重置心跳包自增id
            weblog.on('updateSessionId', () => {
                initHeartBeatIncrementId();
            });
            // 定时上报
            heartBeatTimer = setInterval(() => {
                const log = HeartBeat.getReportData();
                report(log, 'App', true);
                HeartBeat.setPrevTimestamp();
            }, HeartBeat.INTERVAL);
        }],
        onShow: [(param: WechatMiniprogram.App.LaunchShowOption) => {
            const { path, query, scene } = param;
            // 清除判断两次启动时间间隔的定时器
            clearTimeout(appTimer);
            appTimer = 0;

            // 计算两次启动相关，需要存在storage中，不能被清空，因为小程序被杀掉啥的，同样需要保留1
            const currentTime = Date.now();
            const showInterval = currentTime - getStore(STORE_KEY.SHOW_TIME);
            setStore(STORE_KEY.SHOW_TIME, currentTime);

            const enterPath = appendParam(path, query);
            let log = null;
            // 首次冷启动
            // @ts-ignore
            if (getCurrentPages().length === 0 || !isSendFirst) {
                log = genLaunchData(weblog, enterPath, scene, true);
                isSendFirst = true;
            }
            // 两次启动间隔大于一定值的热启动视为另一次启动
            if (showInterval > launchIntervalLimit) {
                weblog && weblog.updateSessionId && weblog.updateSessionId();
                log = genLaunchData(weblog, enterPath, scene, false);
            }
            log && report(log);
        }, () => {
            // heartBeatTimer !== 0 说明onLaunch配置的定时任务还未被销毁，onShow时不需要重新执行。
            if (heartBeatTimer !== 0) { return; }
            HeartBeat.setPrevTimestamp();
            heartBeatTimer = setInterval(() => {
                const log = HeartBeat.getReportData();
                report(log, 'App', true);
                HeartBeat.setPrevTimestamp();
            }, HeartBeat.INTERVAL);
        }],
        onHide: [() => {
            const wait = +backgroundWaitingLimit || 0;
            weblog && weblog.flush();

            const appShowTime = getStore(STORE_KEY.SHOW_TIME);
            const appHideTime = Date.now();

            let stayLength = appHideTime - appShowTime;
            stayLength = stayLength < 0 ? 0 : stayLength;

            const prevStayLength = getStore(STORE_KEY.STAY);
            const totalStayLength = prevStayLength ? prevStayLength + stayLength : stayLength;

            setStore(STORE_KEY.HIDE_TIME, appHideTime);

            setStore(
                STORE_KEY.STAY,
                totalStayLength,
            );

            // 一定时间内没回到前台 发送一次使用时长
            appTimer = setTimeout(() => {
                const reportStayLengthParam = {
                    app_use_duration: getPositiveInteger(getStore(STORE_KEY.STAY)),
                    event_name: EVENT_NAME.U,
                    ...weblog.currentUrlPackage.toJSON(),
                };
                setStore(STORE_KEY.STAY, 0);
                report(reportStayLengthParam);

            }, wait);
        }, () => {
            const log = HeartBeat.getReportData();
            report(log, 'App', true);
            HeartBeat.setPrevTimestamp();
            clearInterval(heartBeatTimer);
            heartBeatTimer = 0;
        }],
        // onError(param: string) {
        //     const errorStack = {
        //         // @ts-ignore
        //         msg: param ? param.message : undefined,
        //         // @ts-ignore
        //         stack: param ? param.stack : undefined,
        //         err: JSON.parse(JSON.stringify(param, Object.getOwnPropertyNames(param))),
        //     };
        //     // 脚本出错或者api接口调用失败
        //     const reportErrorEventParam = {
        //         type: STATUS.E,
        //         message: JSON.stringify(errorStack),
        //         event_name: EVENT_NAME.E,
        //         url_package: {
        //             ...weblog.currentUrlPackage.toJSON(),
        //         }
        //     };

        //     report(reportErrorEventParam);
        // },
    };

    // 通过app.onUnhandledRejection 无效
    // typeof wx.onUnhandledRejection === 'function' && wx.onUnhandledRejection((err) => {
    //     result.onError(err.reason || {});
    // });

    return result;
};
