import type {
    PageData,
    ExtraInfo,
    PublicParams,
    TrackParams,
    PVParams,
    CustomParams,
} from './types';
import type {
    EventType as IEventType,
    EventOptions as IEventOptions,
    ImmediatelyEventOptions,
} from '@/types/mina';
import type { HttpWeblogInitParamsMina } from '@/types/configuration';

import BaseWeblog from '@/core';
import UrlPackage from './url-package';
import Logger from './logger';
import { PAGE_INSTANCE_ATTR, PUBLIC_PARAM, STATE_KEY } from '@/util/mina/types';
import MinaAutoPV from '@/plugins/minaAutoPV';
import { logFactory } from './log';
import {
    getDid,
    didPrefix,
    generateSessionId,
    setDid,
} from '@/util/mina/did';

import {
    UrlObj,
    BaseOptions,
    Weblogger as WebloggerType,
    EntryTagType,
    SupportTypes,
    TaskOperations,
    PVOptions,
    EventTypes,
    ShowOptions,
    TaskOptions,
    CustomOptions,
    EventOptions,
    PageTypeKey,
    ClickOptions
} from '@/config/types';

import Global, { updateConfig } from '@/config/global';
import EntryTag from './entry-tag';
import { initDid } from '@/util/mina/did';
import { initStore } from '@/util/mina/store';
import { getCurrentPageInstance, genUrlIdentity } from '@/util/mina/page';
import { CommonPackage } from './common';


type MinaEventTypes = 'LAUNCH_EVENT' | 'HEART_BEAT_EVENT' | 'APP_USAGE_STAT_EVENT' | 'EXCEPTION_EVENT' | 'DEVICE_STAT_EVENT';

export default class Weblog extends BaseWeblog {
    declare currentUrlPackage: UrlPackage;
    declare referUrlPackage?: UrlPackage;
    declare logger: Logger;
    entryTag: EntryTag | null = null;
    declare logConfig: HttpWeblogInitParamsMina[0];
    override commonPackage: CommonPackage;
    perennialExtra: {[key: string]: any} = {};
    constructor(
        logConfig: HttpWeblogInitParamsMina[0],
        base?: HttpWeblogInitParamsMina[1],
    ) {
        super(logConfig, base);
        this.commonPackage = new CommonPackage(this.baseOption, this.logConfig);
        initStore(); // 初始化小程序的数据缓存
        this.updateCommonPackage({
            device_id: initDid()
        }); // 初始化 did
        this.logger = new Logger(this.logConfig, this.commonPackage);
        this.entryTag = new EntryTag(this as unknown as WebloggerType);
        this.initBuildInPlugins();
        // 永久保存初始化时的h5_extra_attr，否则可能会被重置
        this.setPerennialExtra(this.commonPackage.h5_extra_attr);
        // 设置sessionId
        this.updateSessionId(this.commonPackage.identity_package.device_id);
        // 初始化urlPackage
        this.initUrlPackage();
        this.addPlugins();
    }

    override initUrlPackage() {
        const currentPage = getCurrentPageInstance();
        this.updateCurrentUrlPackage({
            page: (currentPage as any)[PAGE_INSTANCE_ATTR.PAGE],
            params: (currentPage as any)[PAGE_INSTANCE_ATTR.PAGE_PARAMS]
        });
    }

    override updateCurrentUrlPackage(url: string | UrlObj = '', type: PageTypeKey = 'mina') {
        if (typeof url === 'object' && !url.force && this.currentUrlPackage) {
            const { page = this.currentUrlPackage.page, params = {} } = url;
            if (page === this.currentUrlPackage.page) {
                return this.currentUrlPackage.update(page, params);
            }
        }
        this.currentUrlPackage = new UrlPackage(url as UrlObj, type);
    }

    override generateLog<T extends IEventType>(
        eventType: T,
        eventOptions: IEventOptions<T>,
        factory: (type: string, data: any) => any = logFactory,
    ) {
        let { action, params, status, type, eventId, contentPackage, urlPage } = eventOptions as TaskOptions;
        const referUrlPackage = this.referUrlPackage ? this.referUrlPackage?.toJSON() : undefined;
        if (contentPackage) {
            contentPackage = typeof contentPackage === 'string' ? contentPackage : JSON.stringify(contentPackage);
        }
        const commonInfo = {
            currentUrlPackage: this.currentUrlPackage?.toJSON(),
            referUrlPackage,
            contentPackage
        };
        if (eventType === EventTypes.PV) {
            // PV 类型
            const { type } = eventOptions as PVOptions;
            return factory(eventType, {
                ...eventOptions as PVOptions,
                type,
                ...commonInfo
            });
        }
        // 小程序页面飘移问题处理：页面离开时上报的埋点无法严格保证在 PV leave 前上报
        if (typeof urlPage === 'object') {
            const { page, identity, params: newParams } = urlPage;
            if (page && commonInfo.currentUrlPackage.page !== page) {
                commonInfo.currentUrlPackage = {
                    page,
                    identity: identity || genUrlIdentity(page),
                    params: JSON.stringify(newParams),
                    page_type: this.currentUrlPackage?.page_type,
                    category: '',
                };
            } else if (newParams) {
                this.currentUrlPackage?.update('', newParams);
                commonInfo.currentUrlPackage = this.currentUrlPackage?.toJSON();
                if (identity && commonInfo?.currentUrlPackage) {
                    commonInfo.currentUrlPackage.identity = identity;
                }
            }
        }

        if (eventType === EventTypes.SHOW) {
            // SHOW 类型
            const { action } = eventOptions as ShowOptions;
            return factory(eventType, {
                ...eventOptions as ShowOptions,
                name: action,
                ...commonInfo
            });
        }

        if ([EventTypes.CUSTOM, EventTypes.RADAR, EventTypes.CUSTOM_EVENT].indexOf(eventType as EventTypes) !== -1) {
            // CUSTOM 类型
            const { key, value, eventId } = eventOptions as CustomOptions;
            return factory(EventTypes.CUSTOM, {
                ...eventOptions,
                params: value,
                eventId,
                name: key,
                ...commonInfo
            });
        }

        // Radar 日志特殊处理
        if (eventType === EventTypes.RADAR) {
            return factory(eventType, eventOptions);
        }

        // TASK 类型
        return factory(eventType, {
            ...eventOptions,
            params,
            status,
            taskType: type,
            eventId,
            name: action,
            ...commonInfo
        });
    }
    /**
     * 更新 refer url 信息
     * @param url
     * @param type
     */
    override updateReferUrlPackage(url: UrlObj | UrlPackage = this.currentUrlPackage, type: PageTypeKey = 'mina') {
        super.updateReferUrlPackage(url, type);
    }

    /**
     * 获取染色参数信息
     */
    getEntryTags() {
        return this.entryTag && this.entryTag.getEntryTags();
    }
    /**
     * 设置染色参数
     * @param entryTag
     */
    setEntryTag(entryTag: EntryTagType) {
        return this.entryTag && this.entryTag.setEntryTag(entryTag);
    }

    /**
     * 消费 entryTags 信息
     * @param subPage
     */
    consumeEntryTag(subPage: string) {
        return this.entryTag && this.entryTag.consumeEntryTag(subPage);
    }

    /**
     * 清空 entryTags 信息
     * @param subPage
     * @param containSelf
     */
    clearEntryTag(subPage: string, containSelf: boolean = true) {
        return this.entryTag && this.entryTag.clearEntryTag(subPage, containSelf);
    }
    initBuildInPlugins() {
        const {
            minaAutoPV,
        } = this.logConfig;
        if (minaAutoPV) {
            this.plug(MinaAutoPV);
        }
    }

    init(logConfig: HttpWeblogInitParamsMina[0], base?: HttpWeblogInitParamsMina[1]) {
        // 替换参数
        if (logConfig.batchReportInterval) {
            logConfig.wait = logConfig.batchReportInterval;
            delete logConfig.batchReportInterval;
        }
        return new Weblog(logConfig, base);
    }

    track(params: TrackParams) {
        const {
            operation,
            elementAction,
            elementParam,
            contentPackage,
            status,
            type,
        } = params;
        this.collect(operation as SupportTypes, {
            action: elementAction!,
            params: elementParam,
            type,
            status,
            contentPackage,
        } as IEventOptions<'CLICK'>);
    }
    sendPV(params: PVParams) {
        const {
            type,
            actionType,
            status,
            elementAction,
            elementParam,
            contentPackage,
        } = params;
        const sendType = type === 'ELEMENT' ? 'SHOW' : 'PV';
        this.collect(sendType, {
            action: elementAction!,
            actionType,
            params: elementParam,
            name: elementAction,
            type,
            status,
            contentPackage,
        } as IEventOptions<'PV'>);
    }

    setPage({ page, pageParam: params }: PageData) {
        // 手动设置下referUrlpackage
        if (this.currentUrlPackage?.page && this.currentUrlPackage?.page !== page) {
            this.updateReferUrlPackage();
        }
        this.updateCurrentUrlPackage({
            page,
            params
        });
    }

    sendCustomEvent(params: CustomParams) {
        this.collect('CUSTOM', params);
    }

    private genWebloggerParams(params: PublicParams) {
        const { did,
            uid,
            unionid,
            openid } = params;
        return {
            device_id: did,
            user_id: uid,
            union_id: unionid,
            open_id: openid,
            ...params,
        };
    }

    setPublicParam(params: PublicParams) {
        const weblogParams = this.genWebloggerParams(params);
        const { did } = weblogParams;

        if (did) {
            const preDid = getDid();

            // preDid是webLogger生成的，在extraInfo里设置标识
            if (preDid && preDid.indexOf(didPrefix) >= 0) {
                this.setExtraInfo({
                    ksminaDid: preDid,
                }, true);
            }
            // 如果当前session_id是由webLogger生成的did拼接而成的，更新session_id，尽量减少不准确的数据。
            // 在小程序App.onShow时，会触发session_id的更新。
            // 使用方应该在登录成功后调用setPublicParam，更新common_package数据。
            if (Global.sessionId && Global.sessionId.indexOf(didPrefix) >= 0) {
                this.updateSessionId(did);
            }
            setDid(did);
        }

        // 更新common_package中的参数
        for (const key in weblogParams) {
            if (Object.prototype.hasOwnProperty.call(weblogParams, key)) {
                const element = weblogParams[key as keyof typeof weblogParams];
                const shouldUpdate = element !== undefined;
                shouldUpdate && this.updateCommonPackage({
                    [key]: element,
                });
            }
        }

        // identity_package中的数据设置完成
        updateConfig(STATE_KEY.IDENTITY_PACKAGE_READY, true);
    }

    sendExceptionEvent(params: any) {
        this.collect('EXCEPTION_EVENT' as SupportTypes, params);
    }

    setSystemInfo() {
        // 兼容保留
    }

    setLoggerSessionUrl(url: string) {
        this.logger.config.env = 'logger';
        this.logger.updateUrls(url);
    }

    setExtraInfo(extraInfo: ExtraInfo, append: boolean = false) {
        const extraAttr = {
            ...this.perennialExtra,
            ...extraInfo
        }

        if (append) {
            // updateCommonPackage 默认是append
            this.updateCommonPackage({ h5_extra_attr: extraAttr });
            return;
        }
        // 非append 需要先清空h5_extra_attr
        this.commonPackage.h5_extra_attr = {};
        this.updateCommonPackage({ h5_extra_attr: extraAttr });
    }

    setPerennialExtra(extraInfo: ExtraInfo) {
        this.perennialExtra = {
            ...this.perennialExtra,
            ...extraInfo
        };
        this.setExtraInfo({}, true);
    }

    updateSessionId(did?: string) {
        updateConfig(PUBLIC_PARAM.SESSION_ID, generateSessionId(did));
        this.emit('updateSessionId');
    }

    setCommonPackageReady() {
        // identity_package中的数据设置完成
        updateConfig(STATE_KEY.IDENTITY_PACKAGE_READY, true);
    }
    /**
     * 事件发送前的处理
     * @param type
     * @param options
     */
    beforeSend<T extends IEventType>(type: T, options?: IEventOptions<T>) {
        // 点击事件可以设置染色参数
        if (type === 'CLICK') {
            const entryTag = (options as ClickOptions)?.entryTag;
            entryTag && this.setEntryTag({
                page_name: this.currentUrlPackage?.page,
                element_action: (options as IEventOptions<'CLICK'>)?.action,
                ...entryTag
            });
        } else if (type === 'PV') {
            const pvOption = options as PVOptions;
            const pvType = pvOption?.type;
            if (!pvType || pvType === 'enter') {
                if (!this.currentUrlPackage?.page && pvOption?.page) {
                    this.updateCurrentUrlPackage({
                        page: pvOption.page,
                        params: pvOption.params,
                    });
                }
                const id = this.currentUrlPackage?.identity;
                this.clearEntryTag(id);
                this.consumeEntryTag(id);
            }
        }
    }
    /**
     * 收集上报
     * @param type
     * @param options
     * @returns
     */
     override collect<T extends IEventType>(type: T, options: IEventOptions<T> = {}): void {
        this.send(type, options);
    }

    override sendImmediately<T extends IEventType>(type: T, options: ImmediatelyEventOptions<T> = {}): void {
        this.send(type, options, true);
    }

    override send<T extends IEventType>(
        type: T,
        params: ImmediatelyEventOptions<T>,
        immediately?: boolean,
    ) {
        this.beforeSend(type, params);
        const log = this.generateLog(type, params);
        // 雷达日志单独处理
        if (type === 'RADAR') {
            return this.logger.sendRadar(log, (params as ImmediatelyEventOptions<'RADAR'>)?.serviceName);
        }
        const callback = typeof params === 'object' && params.callback || undefined;
        this.logger.send(log, !!immediately, callback);
    }
}
