import type { Weblogger, EntryTagType } from '@/config/types';

// 原则： setEntryTag 之后必须 consumeEntryTag 否则染色信息不准确。

export default class EntryTag {
    weblog: Weblogger;
    private _tagQueue: EntryTagType[] = []; // 存放真实entrytag列表和顺序
    private _stashQueue: EntryTagType[] = []; // 存放pending数据和顺序
    private _identityQueue: string[] = []; // 存放identity信息和顺序
    constructor(weblog: Weblogger) {
        this.weblog = weblog;
    }

    /**
     * 获取 entryTags 信息
     */
    getEntryTags() {
        return this._tagQueue;
    }

    /**
     * 设置 entryTag 信息
     * @param entryTag
     */
    setEntryTag(entryTag: EntryTagType) {
        return this._stashQueue.push(entryTag);
    }

    /**
     * 判断是否能消费
     * @param entryTag
     */
    private shouldConsume() {
        return this._stashQueue.length > 0;
    }

    private pushState(identity: string) {
        this._tagQueue.push(this._stashQueue.pop() as EntryTagType);
        this._identityQueue.push(identity);
    }
    private ifIdentityEmpty(identity: string) {
        if (!identity) {
            throw new Error('Identity is required');
        }
    }
    private ifIdentityRepeat(identity: string) {
        if (this._identityQueue.indexOf(identity) > 0) {
            throw new Error(`Identity ${identity} is already exist. Please change a new one.\n Current identity list: ${this._identityQueue.join(', ')}`);
        }
    }
    /**
     * 消费 entryTags 信息
     * @param identity
     */
    consumeEntryTag(identity: string) {

        this.ifIdentityEmpty(identity);

        this.ifIdentityRepeat(identity);

        // 移除之前队列中的日志，防止entryTag变化
        this.weblog.flush();

        const shouldConsume = this.shouldConsume();
        if (!shouldConsume) { return; }

        this.pushState(identity);

        this.updateCommonGlobalAttr();
    }
    private updateCommonGlobalAttr() {
        const entryTag = this.getEntryTags();
        if (entryTag.length) {
            this.weblog.commonPackage.updateGlobalAttr({
                entry_tag: entryTag,
            });
        }
        return entryTag;
    }
    /**
     * 清空 entryTags 信息
     * @param identity
     * @param containSelf
     */
    clearEntryTag(identity: string, containSelf: boolean = true) {
        this.ifIdentityEmpty(identity);

        let index = this._identityQueue.indexOf(identity);
        if (index === -1) {
            console.warn(`Identity ${identity} isn't exist`);
            return;
        }
        index = !containSelf ? index + 1 : index;
        let pos = this._identityQueue.length - index;
        while (pos > 0) {
            this._tagQueue.pop();
            this._identityQueue.pop();
            pos--;
        }

        this.updateCommonGlobalAttr();
    }
}
