import type { ReportEvent } from '@/core/logger';
import type { SendCallback, LoggerSender } from '@/config/types';
import type { TrackerConfig } from './types';
import type { CommonPackage } from './common';

import { BatchLog as BaseLogger } from '@/core/logger';
import { STATE_KEY } from '@/util/mina/types';
import { sendData } from '@/io/mina';
import Global from '@/config/global';
import { getDid } from '@/util/mina/did';
import { getReportUrl } from '@/util/test';
import { dataTrackHost, buildUrl, Apis } from '@/config/apis';
import type { ResponseType } from '@/io/types';
import { getStore, setStore } from '@/util/mina/store';
import { mp } from '@/util/mina/global';
/**
 * env 为 logger 时上报的 url
 */
const getLoggerReportUrl = () => {
    try {
        let did = getDid();
        did = did.includes('wxo') ? did.replace('wxo', 'web') : `web_${did}`;
        mp.request({ url: `${dataTrackHost}/rest/${did}`, method: 'GET' });
        return getReportUrl(dataTrackHost, `${dataTrackHost}/rest`, did);
    } catch (err) {
        console.warn('request error', err);
    }
};

export default class Logger extends BaseLogger {
    continueSend: Boolean = false;
    // 服务端控制的采样发送，可以发送合法时间，当前时间戳大于这个时间戳，才可以发送
    sendEffectiveTime: undefined | number;
    // 采样存储 key，作为实例化的一个键值，避免多实例互相干扰
    override baseSendData: LoggerSender = data => {
        const sendPromise = sendData(data);
        if (!this.isSetSamplingResult) {
            this.setResponseSamplingConfig(sendPromise);
            this.isSetSamplingResult = true;
        }

    }
    constructor(config: any, commonPackage: CommonPackage) {
        super(config, commonPackage);
        this.getResponseSamplingStorage();
    }

    // 获取 storage 判断是否有过期时间
    getResponseSamplingStorage() {
        try {
            const storageEffectiveTime = Number(getStore(this.responseSamplingStorageKey));
            if (storageEffectiveTime && (Date.now() < storageEffectiveTime)) {
                this.sendEffectiveTime = storageEffectiveTime;
            } else if (storageEffectiveTime) {
                // 如果采样已经过期，清除 storage
                setStore(this.responseSamplingStorageKey, '');
            }
        } catch (e) {
            console.error(e);
        }
    }

    override sendLogs(logs: ReportEvent[], callback?: SendCallback): boolean {
        if (this.shouldSendPackage()) {
            super.sendLogs(logs, callback);
            return true;
        }
        return false;
    }
    /**
     * 根据配置生成上报的 url 地址
     */
    override updateUrls(url?: string) {
        const { env, logger } = this.config;
        if (logger || env === 'logger') {
            if (url) {
                mp.request({ url, method: 'GET' });
                return this.url = url + Apis.v3;
            }
            const loggerEnv = getLoggerReportUrl();
            if (loggerEnv) {
                this.url = buildUrl(loggerEnv);
            }
        } else if (env && /^(https?:)?\/\//.test(env)) {
            this.url = env;
        } else {
            this.url = buildUrl(env);
        }
        this.formatUrl();
    }
    shouldSendPackage() {
        // IS_PUBLIC_PARAM_INIT_READY 公共参数是否设置完成 （网络 项目配置等）
        const commonPackageDone = !!Global[STATE_KEY.APP_DEVICE_PACKAGE_READY];
        // IS_BIZ_PUBLIC_PARAM_READY 账号等必要参数是否设置完成（uid unionid session_key等）
        // 配置sendUtilIdentityDone: false 则不等待
        const identityPackageDone = (
            Global[STATE_KEY.IDENTITY_PACKAGE_READY] || !(this.config as TrackerConfig).sendUtilIdentityDone
        );
        return commonPackageDone && identityPackageDone;
    }
    setResponseSamplingConfig(responsePromise?: Promise<ResponseType | undefined>) {
        responsePromise?.then(response => {
            const expireSeconds = response?.expireSeconds;
            if (expireSeconds) {
                const effectiveTime = Date.now() + expireSeconds * 1000;
                this.sendEffectiveTime = effectiveTime;
                setStore(this.responseSamplingStorageKey, String(effectiveTime));
            }
        });
    }

    override flush = (callback?: SendCallback) => {
        const clear = this.sendLogs(this.throttleQueue.concat(this.asyncQueue), callback);
        if (clear) {
            this.throttleQueue = [];
            this.asyncQueue = [];
        }
    }

    override send(data: ReportEvent, immediate = false, callback?: SendCallback) {
        super.send(data, immediate, callback);
    }

    completeLog(log: any) {
        log.session_id = Global.sessionId;
        return log;
    }
}
