import { proxyLifeCycle, LifecycleConfig as Config, VoidFn } from './utils';

type AppLifeCycle =
    'onLaunch' |
    'onShow' |
    'onHide' |
    // 'onError' |
    'onPageNotFound' |
    'onUnhandledRejection';

export { LifecycleConfig } from './utils';

export function proxyApp(config: Config) {
    if (App) {
        const originApp = App;
        App = function (appOptions: WechatMiniprogram.App.Option) {
            proxyLifeCycle(config, appOptions);
            return originApp(appOptions);
        } as WechatMiniprogram.App.Constructor;
    }
}

const lifecycleConfig: Config = {};

export function use(lifecycle: AppLifeCycle, fn: VoidFn): void {
    if (!lifecycleConfig[lifecycle]) {
        lifecycleConfig[lifecycle] = [];
    }

    (lifecycleConfig[lifecycle] as VoidFn[]).push(fn);
}

export function useOnLaunch(fn: VoidFn) {
    if (!fn) { return; }
    const key: AppLifeCycle = 'onLaunch';
    use(key, fn);
}

export function useOnShow(fn: VoidFn) {
    if (!fn) { return; }
    const key: AppLifeCycle = 'onShow';
    use(key, fn);
}

export function useOnHide(fn: VoidFn) {
    if (!fn) { return; }
    const key: AppLifeCycle = 'onHide';
    use(key, fn);
}

// export function useOnError(fn: VoidFn) {
//     if (!fn) { return; }
//     const key: AppLifeCycle = 'onError';
//     use(key, fn);
// }

export function useOnPageNotFound(fn: VoidFn) {
    if (!fn) { return; }
    const key: AppLifeCycle = 'onPageNotFound';
    use(key, fn);
}

export function useOnUnHandleRejection(fn: VoidFn) {
    if (!fn) { return; }
    const key: AppLifeCycle = 'onUnhandledRejection';
    use(key, fn);
}

export function done() {
    return proxyApp(lifecycleConfig);
}
