export interface AnyObject {
    [key: string]: any;
}
export function proxy(source: AnyObject, name: string, replacement: (...args: any[]) => any, isForced = false): void {
    if (name in source || isForced) {
        const original = source[name];
        const wrapped = replacement(original);
        if (typeof wrapped === 'function') {
            source[name] = wrapped;
        }
    }
}

export type VoidFn = (args: any) => void;

export interface LifecycleConfig {
    [key: string]: VoidFn[] | VoidFn | undefined;
}
export function proxyLifeCycle(config: LifecycleConfig, hostOptions: WechatMiniprogram.App.Option) {
    for (const method in config) {
        if (Object.prototype.hasOwnProperty.call(config, method)) {
            let fnList = config[method];
            proxy(
                hostOptions,
                method,
                (originMethod?: VoidFn) => {
                    return function (...args: any): void {
                        if (fnList) {
                            if (!Array.isArray(fnList)) {
                                fnList = [fnList];
                            }
                            (fnList as VoidFn[])!.forEach(fn => {
                                // @ts-ignore
                                fn.apply(this, args);
                            });
                        }
                        if (originMethod) {
                            // @ts-ignore
                            originMethod.apply(this, args);
                        }
                    };
                },
                true
            );
        }
    }

}
