import type { CommonPackageOptions } from '@/core/common';
import { CommonPackage as BaseCommonPackage } from '@/core/common';
import Cookie from '@/util/mina/cookie';
import { getSystemInfo, getNetworkType } from '@/util/mina/systeminfo';
import { STATE_KEY } from '@/util/mina/types';
import { updateConfig } from '@/config/global';
import { error } from '@/util/console';
import { mp, getMpContainer } from '@/util/mina/global'

export class CommonPackage extends BaseCommonPackage {
    constructor(options: CommonPackageOptions, logConfig: any) {
        super({
            container: getMpContainer(),
            ...options
        });
        this.updateSystemInfo(options || {});
    }
    updateSystemInfo(options: CommonPackageOptions) {
        getSystemInfo((systemInfo) => {
            if (systemInfo) {
                const { platform, language, system, model, brand, screenHeight, screenWidth, pixelRatio, mpSDKVersion, mpVersion } = systemInfo;
                this.update({
                    platform,
                    app_package: {
                        language,
                    },
                    device_package: {
                        os_version: system,
                        ua: '',
                        model: JSON.stringify({
                            model,
                            brand,
                        })
                    },
                    ...options,
                    h5_extra_attr: {
                        screen_with: Math.floor(screenWidth),
                        screen_height: Math.floor(screenHeight),
                        device_pixel_ratio: pixelRatio,
                        resolution: `${Math.floor(screenWidth * pixelRatio)}x${Math.floor(screenHeight * pixelRatio)}`,
                        mpSDKVersion,
                        mpVersion,

                        ...options.h5_extra_attr
                    },
                });
            }
            updateConfig(STATE_KEY.APP_DEVICE_PACKAGE_READY, true);
        });
        getNetworkType((networkType) => {
            this.update({
                network_package: {
                    type: networkType
                }
            });
        });
    }
    /**
     * 检查必填参数
     */
    checkMustProps() {
        if (typeof this.app_package.product_name !== 'string') {
            error('请设置有效的 product_name 值!');
        }
    }

    override toJSON() {
        if (!this.identity_package.user_id) {
            this.identity_package.user_id = Cookie.get('userId') || Cookie.get('userName') || '';
        }
        return super.toJSON();
    }
}
