import type { UrlObj, PageTypeKey } from '@/config/types';
import BaseUrlPackage from '@/core/url-package';
import {
    genUrlIdentity,
} from '@/util/mina/page';
export default class UrlPackage extends BaseUrlPackage {
    constructor(url: UrlObj, pageType: PageTypeKey) {
        super(url, pageType);
        this.identity = this.page ? this.generatePageId(this.page) : '';
    }
    override generatePageId(page: string) {
        return genUrlIdentity(page);
    }
}