import { mp } from './global';
import { networkType } from '@/config/types';

type SystemInfo = {
    model?: string;
    system?: string;
    platform?: number;
    pixelRatio?: number;
    screenHeight?: number;
    screenWidth?: number;
    windowHeight?: number;
    windowWidth?: number;
    language?: string;
    brand?: string;
    // 客户端(微信、支付宝、百度)版本
    mpVersion?: string;
    // 客户端基础库版本
    mpSDKVersion?: string;
};

export const getPlatformPB = (platform: 'ios' | 'android' | 'windows' | 'mac', model: string) => {
    switch (platform?.toLocaleLowerCase()) {
        case 'ios':
            if (/ipad/i.test(model)) {
                return 4; // iPad
            }
            return 3; // iPhone
        case 'android':
            return 1; // Android Phone
        case 'mac':
            return 5; // Mac PC
        case 'windows':
            return 5; // Windows PC
    };
};

export const getNetworkTypePB = (network: string) => {
    network = network.toLocaleLowerCase();
    return networkType[network as keyof typeof networkType] || networkType.unknown;
};

export const getSystemInfo = (() => {
    let info: SystemInfo;
    return (callback:(param: any) => void) => {
        if (info) {
            callback(info);
        };
        try {
            mp.getSystemInfo({
                success(systemInfo: any = {}) {
                    info = {
                        ...systemInfo,
                        mpVersion: systemInfo.version,
                        mpSDKVersion: systemInfo.SDKVersion,
                        platform: getPlatformPB(systemInfo.platform, systemInfo.model) || 0,
                    };
                },
                complete() {
                    callback(info);
                },
            });
        } catch (err) {
            callback(info);
        }
    };
})();

export const getNetworkType = (() => {
    let network: number;
    return (callback:(param: number) => void) => {
        if (network) {
            callback(network);
        }
        try {
            mp.getNetworkType({
                success(res: any) {
                    network = getNetworkTypePB(res.networkType);
                },
                complete() {
                    callback(network);
                }
            });
        } catch (err) {
            callback(network);
        }
    };
})();
