import type { MinaWeblogInstance } from '@/types/index';

import { BasePlugin } from '../base';
import { APP } from '@/util/mina/types';

import genAppProxyConfig from './app';
import { proxyApp, LifecycleConfig as AppLifeCycleConfig } from './lifecycle-proxy/app';

interface Config {
    launchIntervalLimit?: number;
    backgroundWaitingLimit?: number;
    App?: AppLifeCycleConfig;
}
interface ReportMsg {
    [key: string]: number | string | boolean;
}

const defaultLifeHookTrackConfig: Config = {
    launchIntervalLimit: 30000,
    backgroundWaitingLimit: 0,
};

export default class MinaAutoPV extends BasePlugin {

    static override key = 'minaAutoPV';
    private config: Config = {};

    /**
     * 兼容写法，如果在 Weblog 初始化设置 plugins 时，不需要设置 weblog
     * @param weblog
     */
    constructor(weblog?: MinaWeblogInstance, localGlobal: any = global || {}, config: Config = defaultLifeHookTrackConfig) {
        super();
        this.config = config;
        if (weblog) {
            this.apply(weblog);
        }
        // 劫持小程序的生命周期
        this.proxy();
    }

    proxy() {
        // 劫持App生命周期
        proxyApp(this.getAppConfig());

    }

    // 获取App生命周期的配置
    getAppConfig() {
        const appConfig = this.config.App ? this.config.App : genAppProxyConfig(
            this.send.bind(this),
            this.weblog,
            this.config.launchIntervalLimit,
            this.config.backgroundWaitingLimit,
        );
        return appConfig;
    }

    override apply(weblog: MinaWeblogInstance) {
        if (!weblog?.collect) return;
        this.weblog = weblog;
    }

    send(data: ReportMsg, type: string = APP, immediate: boolean = false) {
        if (!data) { return; }
        const eventName = data && data.event_name ? data.event_name as string : 'CUSTOM_STAT_EVENT';
        const sendType = type === APP
            ? eventName : 'SHOW';
        delete data.event_name;
        if (immediate) {
            // @ts-ignore
            this.weblog.sendImmediately(sendType, {
                ...data,
            });
        } else {
            // @ts-ignore
            this.weblog.collect(sendType, {
                ...data,
            });
        }
    }

    destroy() {
        console.log('destroy');
    }
}
