import { getDid } from './did';

const emptyPage = {
    route: '',
    options: {},
};
// 获取当前页面实例
// 后面的空对象是为了兼容怪异手机上微信拿不到页面实例的Bug，使之不报错
export const getCurrentPageInstance = () => getCurrentPages()[getCurrentPages().length - 1] || emptyPage;

export const genUrlIdentity = (url: string) => `${url}_${Date.now()}_${getDid()}`;