import { setStore, getStore } from './store';

const KS_MINA_DID = 'ks_mina_did';


export const didPrefix = 'wxo_';

export const generateDid = () => didPrefix + randomHex(36);

export const randomHex = (length: number) => {
    let res = '';
    for (let i = 0; i < length; i++) {
        res += (Math.random() * 16 | 0).toString(16);
    }
    return res;
};

export const initDid = () => {
    let did = getDid();
    // did前缀修改成wxo了，老的需要重新设置
    if (!did || did.indexOf(didPrefix) === -1) {
        did = generateDid();
        setDid(did);
    }
    return did;
};

export const getDid = () => getStore(KS_MINA_DID);

export const setDid = (did: string) => did && setStore(KS_MINA_DID, did);

export const generateSessionId = (did?: string) => {
    const realDid = did ? did : getDid();
    if (!realDid) {
        return '';
    }
    return realDid + Date.now();
};
