import { xhrErrorLog } from './util';
import { error } from '@/util/console';
import type { ResponseType } from '@/io/types';
import { mp } from '@/util/mina/global';

interface DataOptions {
    url: string;
    data: any;
    async?: boolean;
    useBeacon?: boolean;
    timeout?: number;
    isV2?: boolean;
}

export const sendData = async ({ url, data, timeout }: DataOptions,
    callback?: (error?: any) => ResponseType,
): Promise<ResponseType | undefined> => {
    data = JSON.stringify(data);
    try {
        return new Promise(resolve => {
            mp.request({
                url,
                data,
                header: {
                    'content-type': 'text/plain',
                },
                headers: {
                    'content-type': 'text/plain',
                },
                method: 'POST',
                timeout,
                success(res: {
                    data: ResponseType,
                }) {
                    resolve(res?.data);
                },
                fail(err: Error) {
                    xhrErrorLog(err, url, data);
                    resolve(undefined);
                    callback && callback(err);
                },
            });
        });

    } catch (err) {
        xhrErrorLog(err, url, data);
        callback && callback(err);
    }
};

export const get = (url: string) => {
    const errorHandler = (err: any) => error('[error 401]', `GET 请求出错 url: ${url}`, err);
    try {
        mp.request({
            url,
            method: 'GET',
            header: {
                'content-type': 'text/plain',
            },
            headers: {
                'content-type': 'text/plain',
            },
            fail: errorHandler,
        });
    } catch (err) {
        errorHandler(err);
    }
};
