import Storage from './storage';

const STORE_KEY = 'weblogger_store';
const INIT_STORE_DATA = {};

const initStore = () => {
    const store = Storage.get(STORE_KEY);

    if (!store) {
        Storage.set(STORE_KEY, INIT_STORE_DATA);
    }
};

const setStore = (key: string, val: string | number) => {
    const store = Storage.get(STORE_KEY);
    if (store) {
        if (typeof val === 'object' && !Array.isArray(val)) {
            const t: { [key: string]: any } = {};
            Object.keys(val).forEach((k: string) => {
                t[k] = val[k];
            });

            store[key] = { ...(store[key] || {}), ...t };
        } else {
            store[key] = val;
        }
        Storage.set(STORE_KEY, store);
    }
};

const getStore = (key: string) => {
    try {
        return Storage.get(STORE_KEY)[key];
    } catch (e) {
        console.warn('get storage error', e);
        return null;
    }
};

export {
    initStore,
    getStore,
    setStore,
};
