type MPType = 'MP_WECHAT' | 'MP_ALIPAY' | 'MP_BAIDU' | 'MP_KUAISHOU';

type KeyType = 'WECHART' | 'ALIPAY' | 'BAIDU' | 'KUAISHOU';

export const MP: {[key in KeyType]: MPType} = {
    WECHART: 'MP_WECHAT',
    ALIPAY: 'MP_ALIPAY',
    BAIDU: 'MP_BAIDU',
    KUAISHOU: 'MP_KUAISHOU',
};

export function getMpContainer(): MPType {
    try {
        // @ts-ignore
        if (typeof wx === 'object' && Object.keys(wx).length) {
            return MP.WECHART;
        }
        // @ts-ignore
        if (typeof my === 'object' && Object.keys(my).length) {
            return MP.ALIPAY;
        }
        // @ts-ignore
        if (typeof swan === 'object' && Object.keys(swan).length) {
            return MP.BAIDU;
        }
        // @ts-ignore
        if (typeof ks === 'object' && Object.keys(ks).length) {
            return MP.KUAISHOU;
        }
        return MP.WECHART;
    } catch (e) {
        return MP.WECHART;
    }
}

export function getMpObject() {
    if (typeof wx === 'object') {
        return wx;
    }
    // @ts-ignore
    if (typeof my === 'object') {
        // @ts-ignore
        return my;
    }
    // @ts-ignore
    if (typeof swan === 'object') {
        // @ts-ignore
        return swan;
    }
    // @ts-ignore
    if (typeof ks === 'object') {
        // @ts-ignore
        return ks;
    }
    return wx;
};

export const mp = getMpObject();

export const isAlipay = getMpContainer() === MP.ALIPAY;
