import { getHeartBeatIncrementId } from '@/util/mina/increment-id';
import { getPositiveInteger } from '@/util';

let prevTimestamp = 0;

export const setPrevTimestamp = (timestamp: number = Date.now()) => {
    prevTimestamp = timestamp;
};

export const INTERVAL = 2 * 60 * 1000;

export const getReportData = () => {
    return {
        upload_frequency: INTERVAL,
        seq: getHeartBeatIncrementId() + 1,
        app_use_duration: getPositiveInteger(Date.now() - prevTimestamp),
        event_name: 'HEART_BEAT_EVENT',
    };
};
