import { setStore, getStore } from './store';

const runtimeIdMap: { [key: string]: number } = {};

export const genId = (key: string) => {
    // 自增 id 最大值
    const maxId = 1e8;
    try {
        let id = Number(getStore(key)) || 0;
        if (id + 1 > maxId) {
            id = 0;
        }
        setStore(key, id + 1);
        return id;
    } catch (err) {
        console.warn('setStore Error:', err);
    }
    if (!runtimeIdMap[key]) {
        runtimeIdMap[key] = 0;
    }
    return runtimeIdMap[key]++;
};

// 常规业务事件自增 id key
const LOCAL_INCREAMENT_ID_KEY = 'WEBLOGGER_INCREAMENT_ID_KEY';
// 自定义事件自增 id key
const LOCAL_CUSTOM_INCREAMENT_ID_KEY = 'WEBLOGGER_CUSTOM_INCREAMENT_ID_KEY';

// heart_beat_event 自增id key
const HEART_BEAT_INCREAMENT_ID_KEY = 'WEBLOGGER_HEART_BEAT_INCREAMENT_ID_KEY';


/**
 * 常规事件自增 id
 */
export const getNormalIncrementId = () => {
    return genId(LOCAL_INCREAMENT_ID_KEY);
};

/**
 * 自定义事件自增 id
 */
export const getCustomIncrementId = () => {
    return genId(LOCAL_CUSTOM_INCREAMENT_ID_KEY);
};

/**
 * 心跳事件自增 id
 */
export const getHeartBeatIncrementId = () => {
    return genId(HEART_BEAT_INCREAMENT_ID_KEY);
};

/**
 * 心跳事件自增 id 初始化
 */
export const initHeartBeatIncrementId = () => {
    setStore(HEART_BEAT_INCREAMENT_ID_KEY, 0);
}

